<template>
  <div class="cartcontrol">
    <!--外层动画-->
    <transition name="move">
      <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreasecart">
        <!--内层动画-->
        <span class="inner  icon-remove_circle_outline"></span>
      </div>
    </transition>

    <div class="cart-count" v-show="food.count>0"> {{ food.count }}</div>
    <!--取消了点击的默认行为，阻止冒泡和默认事件，避免穿透-->
    <!--/解决了点击＋按钮同时会跳转页面的问题-->
    <div class="cart-add icon-add_circle" @click.stop.prevent="addCart"></div>
  </div>
</template>
<script >
  import Vue from 'vue'
  export default {
    props: {
      food: {
        type: Object
      }
    },
    methods: {
      addCart () {
        console.log('click')
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1)  // 如果food没有count这个属性，会强行添加一个
        } else {
          this.food.count++
        }
        // 使用$emit(eventName)触发事件add, 并将点击这个事件的dom传给父组件
        // 父组件在使用子组件的地方直接用v-on来监听子组件触发的事件
        this.$emit('add', event.target)
      },
      decreasecart () {
        if (this.food.count) {
          this.food.count--
        }
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .cartcontrol
    font-size: 0
    .cart-decrease
      display: inline-block
      padding: 6px
      opacity:1
      transition: all 0.4s linear
      translate3d(0,0,0)
      .inner
        display: inline-block
        line-height: 24px
        font-size: 24px
        color: rgb(0,160,220)
        transition: all 0.4s linear
        transform: rotate(0)
      &.move-enter, &.move-leave-to
        opacity: 0
        transform: translate3d(24px, 0 , 0)
        .inner
          transform: rotate(180deg)
    .cart-count
      display: inline-block
      vertical-align: top
      width: 12px
      padding-top: 6px
      line-height: 24px
      text-align: center
      font-size: 10px
      color: rgb(147,153,159)
    .cart-add
      display: inline-block
      padding: 6px
      line-height: 24px
      font-size: 24px
      color: rgb(0,160,220)
</style>



